import React from "react";
import './App.css';

class Editor extends React.Component {

  render() {
    return ( 
    <div>
    <h2 style={{textAlign: "center"}}>Editor</h2>
    <textarea
    id = "editor"
    onChange = {this.props.onChange}
    value = {this.props.text}
    />
    </div>
    );
  }
}

export default Editor;